.agenda__navigationItem,
.agenda__navigationItem--active {
    width:175px;
    height: inherit;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;

}

.agenda__navigationItem--active{
    border-bottom: 1px solid blue;
    -webkit-box-shadow: 0px 1px 0px 0px #268EFF;
            box-shadow: 0px 1px 0px 0px #268EFF;
}

.agenda__navigationItem__name,
.agenda__navigationItem__name--active {
    font-size: 20px;

}

.agenda__navigationItem__name{
    color:#AAAAAA;
}

.agenda__navigationItem__name--active {
    color:#444444;
}



