.agenda__content__item,
.agenda__content__item--active {
    height: 65px;
    border-bottom:1px solid #EEEEEE;
    display: -ms-flexbox;
    display: flex;
    width:100%;
    -ms-flex-align: center;
        align-items: center;
    white-space: nowrap;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}

.agenda__content__item:nth-child(even),
.agenda__content__item--active:nth-child(even) {
    background-color: #FFFFFF;
}

.agenda__content__item__order,
.agenda__content__item__order--active {
    width: 110px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    padding-top:5px;
    color:#444444;
}

.agenda__content__item__name,
.agenda__content__item__name--active {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    font-size: 16px;
    padding-top:5px;
    padding-left:5px;
    color:#444444;
}

.agenda__content__item__name--active,
.agenda__content__item__order--active {
    font-weight: 800;
}

.agenda__content__item--active{
    -webkit-animation-name: agenda__content__item__animation;
            animation-name: agenda__content__item__animation;
    -webkit-animation-duration: 0.25s;
            animation-duration: 0.25s;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: 2;
            animation-iteration-count: 2;
}

@-webkit-keyframes agenda__content__item__animation {
    to {
        background-color:#E5F2FF;
    }
}

@keyframes agenda__content__item__animation {
    to {
        background-color:#E5F2FF;
    }
}

.agenda__content__item__order__value{
    width:30px;
    text-align: left;
    white-space: nowrap;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}


